html {
    box-sizing: border-box;
    font-size: 62.5%;
    scroll-behavior: smooth;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    margin: 0px;
    font-family: $sans-serif;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    background-color: $off-white;
}

a {
    color: $brand-1;
    text-decoration: none;
}

.bg-link {
    color: $white;
    text-decoration: none;

    &:hover {
        background-image: linear-gradient(0deg, $brand-1 100%, $brand-1 0%);
        background-repeat: repeat-x;
        background-position: 0 92%;
        background-color: transparent;
        background-size: 200% 2px;
        text-decoration: none;
    }
}

main {
    padding-top: 20px;
}

.wrapper {
    margin: 0 auto;
    padding-right: 10px;
    padding-left: 10px;

    @include mq(tablet) {
        padding-right: 20px;
        padding-left: 20px;
    }

    @include mq(wide) {
        max-width: 100%;
        padding-left: 5%;
        padding-right: 5%;
    }
}

::selection {
    background-color: $black;
    color: $white;
}

.project-title {
    color: black !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    font-weight: normal;

}

