@keyframes animateForwards {
    from {
        top: 0;
        transform: rotate(0deg);
    }
    to {
        top: 8vw;
        transform: rotate(15deg);
    }
}
@keyframes animateBackwards {
    from {
        top: 0;
        transform: rotate(0deg);
    }
    to {
        top: calc(8vw);
        transform: rotate(-15deg);
    }
}

@keyframes animateMixed {
    from {
        top: 0;
    }
    to {
        top: calc(8vw);
    }
}
