.animate {
  display: block;
  position: relative;
  text-align: center;
}

.first {
  margin-left: 0px;
  color: #ff6c6a;
}

.second {
  margin-right: -2em;
  color: #ff6c6a;
}

@-webkit-keyframes animateForwards {
  from {
    top: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    top: 8vw;
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
  }
}

@keyframes animateForwards {
  from {
    top: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    top: 8vw;
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
  }
}

@-webkit-keyframes animateBackwards {
  from {
    top: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    top: calc(8vw);
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
  }
}

@keyframes animateBackwards {
  from {
    top: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    top: calc(8vw);
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
  }
}

@-webkit-keyframes animateMixed {
  from {
    top: 0;
  }
  to {
    top: calc(8vw);
  }
}

@keyframes animateMixed {
  from {
    top: 0;
  }
  to {
    top: calc(8vw);
  }
}

.block__link {
  position: relative;
  cursor: pointer;
  padding: 0.5rem 0;
  color: #ff6c6a;
}

.block__link:hover h2 {
  color: #ff6c6a;
  -webkit-transition: color 0.6s ease;
  transition: color 0.6s ease;
}

.block__link:hover span {
  color: #ff6c6a;
  -webkit-transition: color 0.6s ease;
  transition: color 0.6s ease;
}

.block[data-fx="1"] a::after,
.block[data-fx="2"] a::after,
a::after::hover {
  content: "";
  z-index: -1;
  width: 100%;
  bottom: 0.25rem;
  left: 0;
  position: absolute;
  height: 2px;
  background: black;
  -webkit-transform: scale3d(0, 1, 1);
          transform: scale3d(0, 1, 1);
  -webkit-transform-origin: 0% 50%;
          transform-origin: 0% 50%;
  -webkit-transition: -webkit-transform 0.2s cubic-bezier(0.39, 0.575, 0.565, 1);
  transition: -webkit-transform 0.2s cubic-bezier(0.39, 0.575, 0.565, 1);
  transition: transform 0.2s cubic-bezier(0.39, 0.575, 0.565, 1);
  transition: transform 0.2s cubic-bezier(0.39, 0.575, 0.565, 1), -webkit-transform 0.2s cubic-bezier(0.39, 0.575, 0.565, 1);
  -webkit-transition: color 0.6s ease;
  transition: color 0.6s ease;
}

.block[data-fx="1"] a:hover::after,
.block[data-fx="2"] a:hover::after {
  -webkit-transform: scale3d(1, 1, 1);
          transform: scale3d(1, 1, 1);
}

@-webkit-keyframes loop {
  0% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scale3d(0, 1, 1);
            transform: scale3d(0, 1, 1);
  }
  50% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
  51% {
    -webkit-transform-origin: 100% 50%;
            transform-origin: 100% 50%;
  }
  100% {
    -webkit-transform-origin: 100% 50%;
            transform-origin: 100% 50%;
    -webkit-transform: scale3d(0, 1, 1);
            transform: scale3d(0, 1, 1);
  }
}

@keyframes loop {
  0% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scale3d(0, 1, 1);
            transform: scale3d(0, 1, 1);
  }
  50% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
  }
  51% {
    -webkit-transform-origin: 100% 50%;
            transform-origin: 100% 50%;
  }
  100% {
    -webkit-transform-origin: 100% 50%;
            transform-origin: 100% 50%;
    -webkit-transform: scale3d(0, 1, 1);
            transform: scale3d(0, 1, 1);
  }
}

